import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { BotPageModule } from '../pages/bot/bot.module';
import { BotDetailPageModule } from '../pages/bot-detail/bot-detail.module';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { FaqPage } from '../pages/faq/faq';
import { SettingPage } from '../pages/setting/setting';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    FaqPage,
    SettingPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/bot-detail/bot-detail.module#BotDetailPageModule', name: 'BotDetailPage', segment: 'bot-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/bot/bot.module#BotPageModule', name: 'BotPage', segment: 'bot', priority: 'low', defaultHistory: [] }
  ]
}),
    BotPageModule,
    BotDetailPageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    FaqPage,
    SettingPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



